import React from 'react';
import logo from './logo.svg';
import { Typography } from '@material-ui/core';

function App() {
    return (
        <React.Fragment>
            <img src={logo} alt='logo' style={{ width: '25em' }}/>
            <Typography variant='h1'>SmileApp</Typography>
        </React.Fragment>
    );
}

export default App;
